// Funcin para obtener meta tags por pgina e idioma
export const getPageSEO = (page, language = 'es') => {
  const pageData = seoConfig.pages[page];
  if (!pageData) return null;
  
  return {
    title: pageData.title[language] || pageData.title.es,
    description: pageData.description[language] || pageData.description.es,
    keywords: pageData.keywords[language] || pageData.keywords.es,
    locale: seoConfig.openGraph.locale[language] || seoConfig.openGraph.locale.es
  };
};

// Funcin para generar URL completa
export const getCanonicalUrl = (path = '') => {
  return `${seoConfig.siteUrl}${path}`;
};

// Funcin para generar imagen Open Graph especfica por pgina
export const getPageImage = (page) => {
  const images = {
    home: `${seoConfig.siteUrl}/images/qbapv-home-og.jpg`,
    about: `${seoConfig.siteUrl}/images/qbapv-about-og.jpg`,
    projects: `${seoConfig.siteUrl}/images/qbapv-projects-og.jpg`,
    contact: `${seoConfig.siteUrl}/images/qbapv-contact-og.jpg`,
    blog: `${seoConfig.siteUrl}/images/qbapv-blog-og.jpg`,
    register: `${seoConfig.siteUrl}/images/qbapv-register-og.jpg`
  };
  
  return images[page] || seoConfig.defaultImage;
};
